import axios from 'axios'
import { Toast } from 'vant'
import router from '../router'
import {getToken} from "@/utils/util";
export let HOST_H5 = 'https://www.youjiankang.net/mb'; // h5
export let BASEURL = "http://222.249.173.28:38693/healthhouse/"
// export let BASEURL = "https://www.fastmock.site/mock/fca724c34013e412d6295d8b26b3e9a8/api"
axios.defaults.baseURL = process.env.NODE_ENV == 'development' ? '/api' : BASEURL
axios.defaults.withCredentials = true
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
// axios.defaults.headers['Authorization'] = localStorage.getItem('token') || ""
axios.defaults.headers['uId'] = 0
axios.defaults.headers.post['Content-Type'] = 'application/json'


// 
const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: '',
};


// request 
axios.interceptors.request.use(
    // 
    config => {
      console.log("config", config)
      // token
      config.headers['Authorization'] = getToken()
      //  post 
      // if(config.method == 'post'){
        config.headers.post['Content-Type'] = 'application/json'
      // }
      //  loading 
      Toast.loading('...')
      return config
    },
    // 
    error => {
      //  loading 
      Toast.clear()
      // 
      console.log(error.response,codeMessage[error.response.status])
      // Notify({ type: 'danger', message: codeMessage[error.response.status] });
    }
)

axios.interceptors.response.use(res => {
  if (typeof res.data !== 'object') {
    Toast.fail('')
    return Promise.reject(res)
  }
  console.log(res.data)
  if (res.data.code != 200) {
    if (res.data.message) Toast.fail(res.data.msg)
    if (res.data.code == 416) {
      router.push({ path: '/login' })
    }
    return Promise.reject(res.data)
  }

  return res.data
})

export default axios
